import React from 'react';
import { Form, Input } from 'semantic-ui-react-form-validator'
import { Header, Button, Icon, Grid, Divider } from 'semantic-ui-react';
import { connect } from "react-redux";
import { setTitle } from "./redux/actions";
import store from './redux/store';

function mapDispatchToProps(dispatch) {
  return {
    setTitle: title => dispatch(setTitle(title)),
  };
}

class ConnectedTitle extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: store.getState().title
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ title: event.target.value });
  }

  handleSubmit(event) {
    event.preventDefault();

    this.props.setTitle(this.state.title);
    this.props.history.push('/group');
  }

  render() {
    return [
      <Divider hidden />,
      <Divider hidden />,
      <Grid container centered stackable>
        <Grid.Row centered>
          <Header as='h2' className="ui centered">
            What is your expenditures about?
          </Header>
        </Grid.Row>
        <Grid.Row centered column={1}>
          <Grid.Column>
                <Form onSubmit={ this.handleSubmit } style={{ width: '100%', maxWidth: '500px', margin: '10px auto'}}>
                  <Input type="text" className="big"
                      onChange={this.handleChange}
                      value={this.state.title}
                      validators={['required']}
                      placeholder="Enter the title.."
                      errorMessages={['This field is required']}
                      width={16} />
                    <Divider hidden />
                    <Button style={{ display: 'block', margin: '10px auto' }} size="big" inverted color="green"><Icon name="save"/>&nbsp;Save</Button>
                </Form>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    ];
  }
}

const Title = connect(null, mapDispatchToProps)(ConnectedTitle);

export default Title;
