import React from 'react';
import { Header } from 'semantic-ui-react';
import { connect } from "react-redux";
import { setPeople } from "./redux/actions";
import store from './redux/store';

function mapDispatchToProps(dispatch) {
  return {
    setPeople: people => dispatch(setPeople(people))
  };
}

class ConnectedGroup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      names: store.getState().people
    };
    this.handleNameChanged = this.handleNameChanged.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleNameChanged(event) {
    var names = event.target.value.split(",").map(name => name.trim());
    this.setState({ names: Array.from(new Set(names)) });
  }

  handleSubmit() {
    this.props.setPeople(this.state.names);
    this.props.history.push('/exps');
  }

  render() {
    return (
      <div className="ui center">
        <div className="ui hidden divider"></div>
        <Header as="h2" textAlign="center">Whom did you share expenditures with?</Header>
        <Header as="h3" textAlign="center">Enter the names of people to share</Header>
        <div className="ui one column input big stackable center aligned page grid">
          <div className="column twelve wide">
            <div className="ui left icon fluid input">
              <input type="text" placeholder="Enter seperated with comma(,)" onChange={this.handleNameChanged} defaultValue={this.state.names.join(",")}/>
              <i className="users icon"></i>
            </div>
            <div className="ui hidden divider"></div>
            <div>
            { this.state.names && this.state.names.map((name, idx) => (
              <button className="ui basic button" key={idx}>
                <i className="icon user"></i>
                { name }
              </button>
            ))}
            </div>
            <div className="ui hidden divider"></div>
            <div className="ui hidden divider"></div>
            <button className="ui inverted green big button" onClick={this.handleSubmit}><i className="save icon"></i>&nbsp;Save</button>
          </div>
        </div>
      </div>
    );
  }
}

const Group = connect(null, mapDispatchToProps)(ConnectedGroup);
export default Group;
