import React from 'react';
import { Container, Image, Header, Divider } from 'semantic-ui-react';
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";

import './App.css';

import Title from './Title.js';
import Group from './Group.js';
import Expenditure from './Expenditure.js';

function App() {
  return [
    <Container id="container">
      <Router>
        <Header as='h1' className="ui centered">
          <NavLink exact to="/" className="step">
            <Image src="/logo.png" size='medium' centered />
          </NavLink>
          <Header.Subheader>
            Make settle/calculate easier for expenses spent with your friends.
          </Header.Subheader>
        </Header>
        <Divider hidden />
        <div className="ui fluid ordered steps">
          <NavLink exact to="/" className="step">
            <div className="content">
              <div className="title">Expenditure Title</div>
              <div className="description">Enter the title of the expenditure</div>
            </div>
          </NavLink>
          <NavLink exact to="/group" className="step">
            <div className="content">
              <div className="title">Group Information</div>
              <div className="description">Enter names of people to share</div>
            </div>
          </NavLink>
          <NavLink exact to="/exps" className="step">
            <div className="content">
              <div className="title">Expenditure Details</div>
              <div className="description">Enter expenditure details</div>
            </div>
          </NavLink>
        </div>
        <Route exact path="/" component={Title} />
        <Route exact path="/group" component={Group} />
        <Route exact path="/exps" component={Expenditure} />
      </Router>
    </Container>
  ];
}

export default App;
