const initialState = {
  title: '',
  people: [],
  /*
   * { id: 1, payerId: 1, amount: 0, }
   */
  expenditures: [],
  totalAmount: 0,
  /*
   * { sender: 'Aree', receiver: 'John', amount: 20 }
   */
  transfers: []
};


function getTransfers(exps, people) {
  // 1. get summation of each person
  const payToPerson = (getTotalAmount(exps) / people.length);
  let toPayPerPerson = people.map(name => ({name: name, amount: payToPerson}));
  exps.forEach(exp => {
    toPayPerPerson.filter(pp => pp.name === exp.payer)[0].amount -= exp.amount;
  });

  toPayPerPerson.sort((a,b) => a.amount - b.amount);

  let transfers = [];
  while(toPayPerPerson.length > 1) {
    var payee = toPayPerPerson[0];
    var payer = toPayPerPerson[toPayPerPerson.length - 1];
    transfers.push({ sender: payer.name, receiver: payee.name, amount: payer.amount});

    var sum = payee.amount + payer.amount;
    if(sum === 0) {
      toPayPerPerson.shift();
      toPayPerPerson.pop();
    }
    else if(sum > 0) {
      payer.amount += payee.amount;
      toPayPerPerson.shift();
    }
    else {
      payee.amount += payer.amount;
      toPayPerPerson.pop();
    }
  }
  return transfers;
}

function getTotalAmount(exps) {
  return exps.reduce((acc, expenditure) => acc + expenditure.amount, 0);
}

function rootReducer(state = initialState, action) {
  var newExps = state.expenditures;
  switch (action.type) {
    case 'SET_TITLE':
      state.title = action.title;
      return state;

    case 'SET_PEOPLE':
      return Object.assign({}, state, {
        people: action.people
      });

    case 'DELETE_EXPENDITURE':
      newExps = newExps.filter(s => s.id !== action.id);
      return Object.assign({}, state, {
        expenditures: newExps,
        totalAmount: getTotalAmount(newExps),
        transfers: getTransfers(newExps, state.people)
      });

    case 'SAVE_EXPENDITURE':
      newExps = newExps.filter(s => s.id !== action.id).concat(action.expenditure);
      return Object.assign({}, state, {
        expenditures: newExps,
        totalAmount: getTotalAmount(newExps),
        transfers: getTransfers(newExps, state.people)
      });

    case 'GET_TRANSFERS':
      return Object.assign({}, state, {
        transfers: getTransfers(newExps, state.people)
      });

    default:
      return state
  }
};
export default rootReducer;
