import React from 'react';
import { Header, Select, Input, Label, Table, Message, Divider, Icon, Grid, Button, Segment } from 'semantic-ui-react';
import htmlToImage from 'html-to-image';
import download from 'downloadjs';
import { connect } from "react-redux";
import store from './redux/store';
import { deleteExpenditure, saveExpenditure, getTransfers } from './redux/actions';

function mapDispatchToProps(dispatch) {
  return {
    saveExpenditure: exp => dispatch(saveExpenditure(exp)),
    deleteExpenditure: exp => dispatch(deleteExpenditure(exp)),
    getTransfers: exp => dispatch(getTransfers())
  };
}

let emptyEditableExpenditure = (function() {
  return {
    id: undefined, date: undefined, store: undefined, desc: undefined,
    amount: undefined, payer: undefined
  };
});

class ConnectedExpenditure extends React.Component {
  constructor(props) {
    super(props);

    props.getTransfers();

    this.state = {
      editableExpenditure: emptyEditableExpenditure(),
      showSucceedMessage: false,
      showErrorMessage: false,
      expenditures: store.getState().expenditures,
      transferList: store.getState().transfers
    };

    this.exportToFile = this.exportToFile.bind(this);
    this.copyToClipboard = this.copyToClipboard.bind(this);
    this.saveExpenditure = this.saveExpenditure.bind(this);
    this.handleDismiss = this.handleDismiss.bind(this);
    this.removeErrorMessage = this.removeErrorMessage.bind(this);
    this.editExpenditure = this.editExpenditure.bind(this);
  }

  handleDismiss() {
    this.setState({ showSucceedMessage: false });
  }

  removeErrorMessage() {
    this.setState({ showErrorMessage: false });
  }

  getEditableExpenditureISODate() {
    if (!this.state.editableExpenditure.date) {
      return undefined;
    }
    var curr = new Date(this.state.editableExpenditure.date);
    return curr.toISOString().substr(0,10);
  }

  editExpenditure(expenditure) {
    this.setState({ editableExpenditure: expenditure });
    window.scrollTo(0, 0);
  }

  deleteExpenditure(expenditure) {
    this.props.deleteExpenditure(expenditure);
    let { expenditures, transfers } = store.getState();
    this.setState({ expenditures: expenditures, transferList: transfers });
  }

  saveExpenditure(event) {
    event.preventDefault();

    let { expenditures, editableExpenditure } = this.state;

    let date = document.querySelector("#date").value;
    let state = document.querySelector("#store").value;
    let desc = document.querySelector("#desc").value;
    let amount = parseInt(document.querySelector("#amount").value);
    let payer = document.querySelector("#payer>.text").innerText;
    if(!(date && state && desc && amount && payer)) {
      this.setState({ showErrorMessage: true });
      return;
    }

    if (editableExpenditure.id === undefined) {
      let len = expenditures.length;
      editableExpenditure.id = (len === 0) ? 1 : expenditures[expenditures.length - 1].id + 1;
    }
    editableExpenditure.date = date;
    editableExpenditure.store = state;
    editableExpenditure.desc = desc;
    editableExpenditure.amount = amount;
    editableExpenditure.payer = payer;

    this.props.saveExpenditure(editableExpenditure);

    this.setState({
      expenditures: store.getState().expenditures.sort((a,b) => a.id - b.id),
      transferList: store.getState().transfers,
      editableExpenditure: emptyEditableExpenditure(),
      showSucceedMessage: true,
    });
    this.form.reset();
  }

  exportToFile() {
    var now = new Date();
    var nowString = [now.getFullYear(), now.getMonth() + 1, now.getDate()].join("-");
    htmlToImage.toJpeg(document.getElementById('expenditure_settlement'), {
      backgroundColor: 'white'
    }).then(dataUrl => download(dataUrl, `expenditure.png_${nowString}.jpeg`));
  }

  copyToClipboard() {
    var text = this.state.transferList.map(transfer => `${transfer.sender} -> ${transfer.receiver} : $${Math.round(transfer.amount * 100) / 100}`).join("\n");
    if(!text) {
      alert("Nothing to copy. Enter the expenditures and try copying again!");
      return ;
    }
    var dummy = document.createElement("textarea");
    document.body.appendChild(dummy);
    dummy.value = text;
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
    alert("Copied to clipboard!");
  }

  render() {
    let { title, people, totalAmount } = store.getState();
    let pp = people.length > 0 ? Math.round((totalAmount / people.length) * 100) / 100 : 0;
    let peopleNameOptions = people.map(name => ({ key: name, value: name, text: name }));
    let expenditureHeaders = ['#', 'Date',  'Payee(store) Name', 'Description', 'Paid Amount', 'Payer Name', 'Action'];

    return (
      <div className="ui center">
        <Divider hidden />
        <Header as="h2" textAlign="center">Enter expenditures, and
        <br/>Get to know the efficient settlement!</Header>
        <Divider hidden />

        <form className="ui form" ref={el => this.form = el}>
          <Segment attached='top' piled>
            <Message negative style={{ display: this.state.showErrorMessage ? 'block' : 'none' }}>
              <Message.Header>Failed to save the expenditure</Message.Header>
              <p>Please enter all fields.</p>
            </Message>
            <Message info style={{ display: this.state.showSucceedMessage ? 'block' : 'none' }}
                    onDismiss={this.handleDismiss}>
              <Message.Header>Success!</Message.Header>
              <p>The expenditure is saved.</p>
            </Message>

            <Grid columns='equal' centered stackable>
              <Grid.Row>
                <Grid.Column width={5}>
                  <label>Expense date</label><br/>
                  <input id="date" type="date" defaultValue={this.getEditableExpenditureISODate()}/>
                </Grid.Column>
                <Grid.Column width={6}>
                  <label>Paid amount</label><br/>
                  <Input id="amount" labelPosition='left' type='number'
                         placeholder='(e.g. 2)'
                         defaultValue={this.state.editableExpenditure.amount}
                         onChange={this.removeErrorMessage}>
                    <Label basic>$</Label>
                    <input />
                  </Input>
                </Grid.Column>
                <Grid.Column width={5}>
                  <label>Payer name</label><br/>
                  <Select id="payer" search options={peopleNameOptions}
                          placeholder={this.state.editableExpenditure.payer}
                          defaultValue={this.state.editableExpenditure.payer}
                          onChange={this.removeErrorMessage}/>
                </Grid.Column>
              </Grid.Row>

              <Grid.Row>
                <Grid.Column width={7}>
                  <label>Payee(store) name</label>
                  <input id="store" type="text" placeholder="(e.g. K supermarket)"
                          defaultValue={this.state.editableExpenditure.store}
                          onChange={this.removeErrorMessage}/>
                </Grid.Column>
                <Grid.Column width={9}>
                  <label>Description</label>
                  <input id="desc" type="text" placeholder="(e.g. Buy water)"
                          defaultValue={this.state.editableExpenditure.desc}
                          onChange={this.removeErrorMessage}/>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Segment>
          <Button attached='bottom' onClick={this.saveExpenditure}>
            <i className={`${this.state.editableExpenditure.id ? 'save' : 'add'} icon`}></i>
            {this.state.editableExpenditure.id ? 'Save' : 'Add'}
          </Button>
        </form>

        <Divider hidden />
        <Divider hidden />

        <div id="expenditure_settlement">
          <Header as="h3" textAlign="left">Expenditure Histories of <span style={{ color: 'olive' }}>{title}</span></Header>
          <Table celled selectable sortable color={'olive'}>
            <Table.Header>
              <Table.Row>
                { expenditureHeaders.map((header, key) => <Table.HeaderCell key={key}>{header}</Table.HeaderCell>) }
              </Table.Row>
            </Table.Header>

            <Table.Body>
              { this.state.expenditures.map((expenditure, key) =>
                <Table.Row key={key}>
                  { Object.values(expenditure).map((data, key2) => <Table.Cell key={`${key}_${key2}`}>{data}</Table.Cell>) }
                  <Table.Cell textAlign="center" key={`cell_${key}`}>
                    <div className="ui small basic icon buttons">
                      <button className="ui button" onClick={el => this.editExpenditure(expenditure)}><i className="edit icon"></i></button>
                      <button className="ui button" onClick={el => this.deleteExpenditure(expenditure)}><i className="trash alternate icon"></i></button>
                    </div>
                  </Table.Cell>
                </Table.Row>
              ) }
            </Table.Body>
          </Table>

          <Header as="h4" textAlign="center">Your group spent &nbsp;&nbsp;<span className="ui tag green label">${ totalAmount }</span> in total. &nbsp;&nbsp;<span className="ui tag olive label">${pp}</span> per person.</Header>
          <Divider hidden />
          <Divider hidden />
          <Divider horizontal>
            <Header as='h4'>
              <Icon.Group size='large'>
                <Icon name='money bill alternate outline' />
                <Icon corner name='bar chart' />
              </Icon.Group>
              &nbsp;&nbsp;Settlements
            </Header>
          </Divider>
          <Header as="h4" textAlign="left">This way, you'll be done with the settlement..</Header>
          <Table celled sortable selectable color={'green'} textAlign='center'>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Sender <i className="arrow right icon"></i> Receiver</Table.HeaderCell>
                <Table.HeaderCell>Amount</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
            { this.state.transferList.map(transfer => (
              <Table.Row>
                <Table.Cell>{transfer.sender} <i className="arrow right icon"></i> {transfer.receiver}</Table.Cell>
                <Table.Cell>${Math.round(transfer.amount * 100) / 100}</Table.Cell>
              </Table.Row>
            )) }
            </Table.Body>
          </Table>
        </div>
        <Divider hidden />
        <button className="ui right floated blue button" onClick={ this.exportToFile }><i className="download icon"></i> Download</button>
        <button className="ui right floated grey button" onClick={ this.copyToClipboard }><i className="copy icon"></i> Copy</button>
        <Divider hidden />
        <Divider hidden />
      </div>
    );
  }
}

const Expenditure = connect(null, mapDispatchToProps)(ConnectedExpenditure);
export default Expenditure;
